@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base {

  li {
    @apply p-4
  }

  button {
    @apply text-yellow-50 border bg-qatar4 border-qatar4 
    hover:bg-brightqatar4 rounded-md hover:text-yellow-50
  }

  input[type="submit"] {
    @apply text-yellow-50 border bg-qatar4 border-qatar4 
    hover:bg-brightqatar4 rounded-md hover:text-yellow-50
  }
  

}
